@import "variables";

/* HEADER */
.header{
	transition-duration: 0.2s;
	width: 112px;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 1000;
	padding: 30px 0 0;
	text-align: center;
}

/* NAV */
.nav-trigger{
  position: relative;
  width: 46px;
  height: 50px;
  border: none;
  background: none;
  cursor: pointer;
  text-align: center;
  margin: 0;
  display: inline-block;
  font-family: $font-1bl;
  color: #e51e0b;
  font-size: 14px;
  text-transform: uppercase;
  overflow: hidden;
  padding-top: 37px;
  line-height: 1em;
  text-align: center;
  span{
    width: 46px;
    height: 6px;
    border-radius: 3px;
    background: #e51e0b;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    transition-duration: 0.2s;
  }
  span{
    &:nth-child(2){
      top: 12px;
    }
    &:nth-child(3){
      top: 24px;
    }
  }
}