@import "variables";

/* modal */
.modal-open{
  overflow: hidden;
  .modal{
    overflow-x: hidden;
    overflow-y: auto;
  }
}
 
.modal{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
}

.modal-dialog {
  position: relative;
  pointer-events: none;
  max-width: 407px;
  margin: 92px auto 0.5rem;
}

.modal-dialog-centered {
  display: flex;
  align-items: center;
  min-height: calc(100% - (.5rem * 2));
  margin: 0.5rem auto;
}

.modal.fade .modal-dialog {
  transition: transform 0.3s ease-out;
  transform: translate(0, -50px);
}
.modal.show .modal-dialog {
  transform: translate(0,0);
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  transition-duration: 0.2s;
}
.modal-backdrop.fade {
  opacity: 0;
}
.modal-backdrop.show {
  opacity: 0;
}

.modal button.close {
  position: absolute;
  top: 21px;
  right: 21px;
  width: 24px;
  height: 24px;
  margin: 0;
  opacity: 1;
  background: $color-r url(../images/close.svg) center center no-repeat;
  background-size: 10px 10px;
  border: none;
  border-radius: 50%;
  z-index: 10;
  transition-duration: 0.2s;
  &:hover{
    opacity: 0.8;   
  }
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border-radius: 33px;
  outline: 0;
  color: $color-3;
  font-size: 13px;
  .btn{
    width: 100%;
  }
  h2, .h2{
    text-align: center;
    text-transform: uppercase;
    font-size: 32px;
    font-family: $font-1bl;
    margin: 0;
    line-height: 1.3em;
  }
  h3, .h3{
    text-align: center;
    text-transform: uppercase;
    font-size: 22px;
    font-family: $font-1bl;
    margin: 0 0 3px;
    color: $color-r;
    line-height: 1.3em;
  }
  h4, .h4{
    text-align: center;
    text-transform: uppercase;
    font-size: 18px;
    font-family: $font-1b;
    margin: 0;
    line-height: 1.3em;
  }
  h5, .h5{
    text-align: center;
    text-transform: uppercase;
    font-size: 17px;
    font-family: $font-1bl;
    margin: 0;
    line-height: 1.25em;
    color: $color-4;
  }
  .warning{
    color: $color-r;
  }
}

.modal-head{
  margin-bottom: 12px;
  padding: 17px 10px 0px;
  text-align: center;
  .logo{
    margin-bottom: 10px;
  }
  .logo-mob{
    display: none;
  } 
}

#registration{
  .modal-head{
    margin-bottom: 19px;
  }
}
#finish{
  .modal-head{
    padding-top: 22px;
  }
}

.modal-body{
  padding: 0 30px 25px;
  font-family: $font-1m;
  line-height: 1.3em;
  .message{
    text-align: center;
    margin-bottom: 20px;
  }
  .warning{
    color: $color-r;
  }
}

/* forms */
input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
  background: #fff;
  border: none;
  border-bottom: 1px solid $color-3;
  margin: 0;
  width: 100%;
  height: 34px;
  padding: 0 15px;
  font-family: $font-1m;
  font-size: 13px;
  border-radius: 0;
  display: block;
  color: #a8a8a8;
  text-align: center;
  text-transform: uppercase;
}
textarea{
  padding: 10px 25px;
  height: 150px;
  resize: none;
}

::-webkit-input-placeholder {color:#a8a8a8;transition-duration:0.2s;}
::-moz-placeholder          {color:#a8a8a8;transition-duration:0.2s;}
:-moz-placeholder           {color:#a8a8a8;transition-duration:0.2s;}
:-ms-input-placeholder      {color:#a8a8a8;transition-duration:0.2s;}

:focus::-webkit-input-placeholder {opacity: 0.8;}
:focus::-moz-placeholder          {opacity: 0.8;}
:focus:-moz-placeholder           {opacity: 0.8;}
:focus:-ms-input-placeholder      {opacity: 0.8;}

.form-group{
  margin-bottom: 11px;
  position: relative;
  font-size: 15px;
  &.radio, &.checkbox{
    padding: 0 30px;
    margin-top: 25px;
    margin-bottom: 13px;
    &+&{
      margin-top: 0;
    }
  }
}

/* checkbox/radio */
input[type='checkbox'], input[type='radio'] {
  vertical-align: top;
  width: 20px;
  height: 20px;
  margin: 0 3px 0 0;
}
input[type='checkbox'] + label, input[type='radio'] + label {
  cursor: pointer;
  padding-left: 34px;
  padding-top: 0px;
  position: relative;
  margin-bottom: 0px;
  display: block;
  line-height: 1.3em;
  color: #4fb748;
  font-size: 12px;
  &:before{
    content: '';
    position: absolute;
    left: 0px;
    top: 50%;
    margin-top: -9px;
    width: 18px;
    height: 18px;
    border: 2px solid #4fb748;
    border-radius: 4px;
    border-radius: 50%;
  }
  &:after{
    content: '';
    position: absolute;
    left: 3px;
    top: 50%;
    margin-top: -6px;
    width: 12px;
    height: 12px;
    opacity: 0;
    background: #4fb748;
    border-radius: 2px;
    border-radius: 50%;
  }
}
input[type='radio'] + label {
  &:before{
    border-radius: 50%;
  }
  &:after{
    border-radius: 50%;
  }
}
input[type='checkbox']:not(checked), input[type='radio']:not(checked) {
  position: absolute;
  opacity: 0;
  left: 0;
  top: 0;
  z-index: -1;
}
input[type='checkbox']:checked + label, input[type='radio']:checked + label {
  &:after{
    opacity: 1;
  }
}

/* button */
.btn{
  display: inline-block;
  min-width: 308px;
  padding: 0 15px;
  margin: 15px 0 10px;
  color: $color-1;
  background-color: $color-2;
  border: 1px solid $color-2;
  font-size: 18px;
  line-height: 66px;
  font-family: $font-1b;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  border-radius: 34px;
  position: relative;
  z-index: 0;
  box-shadow: -5px 10px 0 0 rgba(0,0,0,0.14);
  transition-duration: 0.4s;
  overflow: hidden;
  vertical-align: middle;

  &:hover{
    border-color: $color-3;
    background-color: $color-3;
    text-decoration: none;
  }

  &.btn-style-2{
    background-color: $color-1;
    border-color: $color-1;
    color: $color-3;
    &:hover{
      background: #e74810;
      border-color: #e74810;
      color: $color-1;
    }
  }
  
  &.btn-icon{
    padding-right: 65px;
  }
  .icon{
    width: 57px;
    height: 57px;
    display: flex;
    background: #fab237;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 6px;
    top: 50%;
    transform: translateY(-50%);
    svg{
      fill: #fff;
      fill-rule: evenodd;
      max-width: 29px;
      max-height: 29px;
    }
  }
}

.recovery-link{
  text-align: center;
  margin-bottom: 5px;
  font-size: 13px;
  border-bottom: 1px solid $color-3;
  padding: 21px 15px 8px;
  a{
    color: $color-r;
    text-transform: uppercase;
    &:hover{
      text-decoration: none;
    }
  }
}

/* winners */
.tab-content > .tab-pane {
  display: none;
}
.tab-content > .active {
  display: block;
}
.results{
  padding-top: 15px;
  margin: 0 -30px -25px;
  .nav{
    display: flex;
    position: relative;
    z-index: 1;
    padding: 0;
    margin: 0;
    list-style: none;
    padding-bottom: 30px;
    &:after{
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 33px;
      background: #e74810;
      z-index: -1;
    }
    a{
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 0;
      width: 50%;
      height: 65px;
      padding: 8px 15px 3px;
      text-align: center;
      font-size: 18px;
      font-family: $font-1b;
      text-transform: uppercase;
      line-height: 1.3em;
      border-radius: 33px 33px;
      background: $color-1;
      color: $color-3;
      &:first-child{
        &:after{
          content: '';
          position: absolute;
          left: 0;
          right: 50%;
          bottom: -30px;
          top: 50%;
          background: $color-1;
          z-index: -1;
          transition-duration: 0.4s;
        }
      }
      &:last-child{
        &:after{
          content: '';
          position: absolute;
          left: 50%;
          right: 0;
          bottom: -30px;
          top: 50%;
          background: $color-1;
          z-index: -1;
          transition-duration: 0.4s;
        }
      }
      &.active{
        background: #e74810;
        color: #fff;
        &:after{
          background: #e74810 !important;
        }
      }
      img{
        display: block;
        margin: 0;
      }
      &:hover{
        text-decoration: none;
      }
    }
  }
  .tab-content{
    position: relative;
    z-index: 3;
    border: 12px solid #e74810;
    border-radius: 33px;
    padding: 18px 9px;
    background: #fff;
    overflow: hidden;
    margin-top: -30px;
    table{
      margin-bottom: 0;
      tr td:first-child{
        min-width: 105px;
      }
    }
  }
}

/**/
.scroll-box{
  max-height: 286px;
  overflow: hidden;
  padding: 11px 0 8px;
}
.mCSB_scrollTools{

}
.mCSB_inside > .mCSB_container{
  margin-right: 0;
}
.mCSB_scrollTools{
  width: 11px;
}
.mCSB_outside+.mCS-minimal-dark.mCSB_scrollTools_vertical, .mCSB_outside+.mCS-minimal.mCSB_scrollTools_vertical {
    right: 0px;
    margin: 0px 0;
    opacity: 1;
}
.mCSB_scrollTools .mCSB_dragger {
  background: $color-1;
  border-radius: 6px; }

.mCSB_scrollTools .mCSB_draggerRail {
    width: 11px;
    background: #4fb748;
    border-radius: 6px;
}

.mCSB_outside + .mCS-minimal.mCSB_scrollTools_vertical, .mCSB_outside + .mCS-minimal-dark.mCSB_scrollTools_vertical {
  background: #4fb748;
  border-radius: 6px; }

.mCS-minimal.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar, .mCS-minimal.mCSB_scrollTools .mCSB_draggerRail, .mCS-minimal-dark.mCSB_scrollTools .mCSB_draggerRail {
  background: none !important; }

@media only screen and (max-width: 575px) {
  .modal-dialog {
    max-width: 360px;
    margin: 30px auto 0.5rem;
  }
  .modal button.close {
    top: 15px;
    right: 15px;
  }
  .modal-head{
    padding-top: 10px;
    padding-right: 20px;
    padding-left: 20px;
    .logo{
      display: none;
    }
    .logo-mob{
      display: inline-block;
      margin-bottom: 15px;
    }
    h3, .h3{
      font-size: 20px;
    }
  }
  .modal-body {
      padding: 0 15px 20px;
  }

  /**/
  .btn{
    min-width: 100%;
    line-height: 50px;
    font-size: 16px;
    box-shadow: -3px 6px 0 0 rgba(0,0,0,0.14);
    .icon{
      width: 40px;
      height: 40px;
      svg{
        max-width: 20px;
        max-height: 20px;
      }
    }
  }
  .form-group{
    margin-bottom: 10px;
     &.radio, &.checkbox{
      padding: 0 0px;
    }
  }

  .scroll-box{
    max-height: 266px;
    padding: 0;
  }
  .mCSB_scrollTools{
    opacity: 0;
  }

  .modal-content h5, .modal-content .h5 {
    font-size: 16px;
  }
  .results {
    padding-top: 10px;
    margin: 0 -15px -20px;
    .tab-content{
      border-width: 9px;
    }
  }
  .results .nav a{
    font-size: 16px;
    height: 60px;
  }
  .results .tab-content table tr td:first-child{
    min-width: 0;
  }

}