/* first-screen */
.first-screen{
  background: url(../images/bg-first.jpg) center center no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
}

/* logo */
.logo-wrap{
  position: relative;
  z-index: 20;
  margin: 66px auto 0;
  max-width: 825px;
  .rotate-all{
    transform: rotate(-0deg);
  }
  .rotate-box{
    animation-name: logoRotate;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 10s;
    transform-style: preserve-3d;
    transform-origin: 50% 50% 0;

    position: relative;
    width: 825px;
    height: 169px;
  }
  .logo-box{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    &.logo-back{
      transform: rotateY(0deg) rotateX(90deg) translateZ(60px);
    }
    &.logo-front{
      transform: translateZ(60px);
    }
  }
}

.logo-wrap-mobile{
  display: none;
}

@keyframes logoRotate {
  from,to {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(-5deg);
  }
  45% {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(-5deg);
  }
  50% {
    transform: rotateX(-90deg) rotateY(5deg) rotateZ(0deg);
  }
  95% {
    transform: rotateX(-90deg) rotateY(5deg) rotateZ(0deg);
  }
}

/*packs*/
.packs{
  position: absolute;
  bottom: 142px;
  left: 0;
  right: 0;
  z-index: 15;
  .btn-vote{
    background: #fff;
    border: none;
    display: inline-block;
    width: 100%;
    padding: 0 15px;
    margin: 0px 0 28px;
    color: #009c3e;
    font-size: 28px;
    line-height: 80px;
    font-family: $font-1b;
    text-decoration: none;
    text-transform: uppercase;
    text-align: center;
    cursor: pointer;
    border-radius: 40px;
    position: relative;
    z-index: 0;
    box-shadow: -10px 20px 0 0 rgba(0,0,0,0.14);
    transition-duration: 0.4s;
    overflow: hidden;
    vertical-align: middle;
  }

  .share{
    text-transform: uppercase;
    color: #fff;
    font-size: 19px;
    font-family: $font-1b;
    text-decoration: none;
  }

  .pack{
    width: 435px;
    text-align: center;
    .pack-img{
      display: inline-block;
      img{
        animation-duration: 1s;
        animation-fill-mode: both;
      }
    }
  }

  .pack-l{
    position: absolute;
    left: 30px;
    bottom: 0;
    .pack-img{
      max-width: 360px;
      margin-bottom: -25px;
    }
  }
  .pack-r{
    position: absolute;
    right: 30px;
    bottom: 0;
    .pack-img{
      max-width: 326px;
      margin-bottom: -47px;
    }
    .btn-vote{
      color: #e64911;
    }
  }
}

.parallax-layer{
  transform-origin: center;
}

@keyframes bounce {
  from,
  20%,
  53%,
  80%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  40%,
  43% {
    -webkit-animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    -webkit-transform: translate3d(0, -30px, 0);
    transform: translate3d(0, -30px, 0);
  }

  70% {
    -webkit-animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    -webkit-transform: translate3d(0, -15px, 0);
    transform: translate3d(0, -15px, 0);
  }

  90% {
    -webkit-transform: translate3d(0, -4px, 0);
    transform: translate3d(0, -4px, 0);
  }
}

.bounce{
  animation-name: bounce;
}

/* stars */
.stars-wrap{
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  z-index: 10;
  text-align: center;
}
.stars{
  position: relative;
  display: inline-block;
  padding-bottom: 108px;
  max-width: 641px;
  margin-left: 20px;
  .image{
    display: inline-block;
    margin-bottom: -200px; 
    position: relative;
    z-index: 1;
  }
  .stars-inside{
    position: relative;
  }
  path{
    fill: #fff;
      fill-rule: evenodd;
  }
  .sign-l{
    position: absolute;
    bottom: 29px;
    left: 0;
    margin-left: -81px;
    z-index: 3;
    transform: rotate(15deg);
    svg{
      width: 149px;
      height: 41px;
    }
  }
  .sign-r{
    position: absolute;
    bottom: 29px;
    right: 0;
    margin-right: -23px;
    z-index: 3;
    transform: rotate(-15deg);
    svg{
      width: 102px;
      height: 37px;
    }
  }
}

/* next-block */
.next-block{
  position: absolute;
  left: 50%;
  bottom: 14px;
  z-index: 5;
  transform: translateX(-50%);
  display: block;
  color: #000;
  font-size: 14px;
  text-transform: uppercase;
  padding: 0px;
  img{
    margin: 0 8px;
    width: 25px;
  }
  &:hover{
    text-decoration: none;
    color: #000;
  }
}

/* stages */
.stages-box{
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;
  padding: 20px 0;
  background: $color-1;
}
.stages-wrap{
  margin-left: -50px;
  margin-right: -50px;
}
.stages{
  display: flex;
  .slick-track{
    display: flex;
    align-items: center;
  }
  .stage{
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0px 50px;
    position: relative;
    font-size: 16px;
    line-height: 1.3em;
    color: #b1181e;
    font-family: $font-1b;
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateX(50%);
      width: 41px;
      height: 27px;
      margin-top: -13px;
      background: url(../images/arrow.svg) center center no-repeat;
      background-size: contain;
    }
    .image{
      margin-right: 18px;
      img{
        margin: 0;
        display: block;
        height: 60px;
        width: auto;
      }
    }
    p{
      margin: 0;
      span{
        font-size: 24px;
        line-height: 1em;
        font-family: 'Helvetica Bold', sans-serif;
        font-weight: bold;
        word-spacing: -3px;
      }
    }
    .sep{
      color: #fff;
      padding: 0 30px;
      font-size: 24px;
      text-transform: uppercase;
    }
  }
  .stage-w:last-child .stage{
    &:after{
      display: none;
    }
   }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .logo-wrap{
    .rotate-box{
      animation-name: none;
    }
  }
  .stages .stage .image{
    width: 60px;
    img{
      height: auto;
    }
  }
}

