/* second-screen */
.second-screen{
  background: url(../images/bg-second.jpg) center center no-repeat;
  background-size: cover;
  position: relative;
  .container{
    max-width: 1450px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-height: 100%;
    >div{
      width: 100%;
    }
  }
}

.second-screen-head{
  padding: 37px 0 0 0;
  .title-wrap{
    max-width: 1220px;
    margin: 0 auto -30px;
  }
}

.second-screen-body{
  h3, .h3{
    margin-bottom: 25px;
    color: #009c3e;
    font-size: 35px;
    font-family: $font-1b;
    text-align: center;
  }
}

.prizes-carousel{
  margin-bottom: 40px;
  padding: 0 50px;
  .item-wrap{
    padding: 50px 22px;
  }
  .item{
    text-align: center;
    transition-duration: 0.3s;
    .image{
      margin-bottom: 0px;
      text-align: center;
      img{
        display: inline-block;
      }
    }
    .title{
      margin-bottom: 0;
      font-size: 24px;
      font-family: $font-1b;
      color: #e64911;
    }
  }
  .slick-center{
    .item{
       transform: scale(1.2);
       .image{
          margin-bottom: 10px;
        }
       .title{
        color: #009c3e;
      }
    }      
  }
  .slick-arrow{
    width: 61px;
    height: 48px;
    position: absolute;
    top: 50%;
    margin-top: -60px;
    z-index: 10;
    border: none;
    background-color: transparent;
    background-position: left center;
    background-repeat: no-repeat;
    background-size: contain;
    overflow: hidden;
    text-indent: -1000px;
    &.slick-prev{
      left: -10px;
      background-image: url(../images/prev.png);
    }
    &.slick-next{
      right: -10px;
      background-image: url(../images/next.png);
      background-position: right center;
    }
    &.slick-disabled{
      display: none !important;
    }
  }
}

.second-screen-footer{
  max-width: 1325px;
  color: #646464;
  font-size: 9px;
  text-align: center;
  font-family: $font-1;
  padding: 12px 0;
  line-height: 1.2em;
  p{
    margin: 0;
  }
}

@media (max-height: 880px){
  .second-screen-head .title-wrap img{
    max-width: 500px;
  }
  .prizes-carousel .item .image img{
    max-height: 300px;
  }
}
@media (max-height: 805px){
  .second-screen-head .title-wrap img{
    max-width: 450px;
  }
  .prizes-carousel .item .image img{
    max-height: 250px;
  }
  .second-screen-body h3, .second-screen-body .h3{
    margin-bottom: 5px;
  }
  .prizes-carousel{
    margin-bottom: 20px;
  }
}
@media (max-width: 1400px){
  .second-screen-head{
    .title-wrap{
      img{
        margin-left: 15%;
      }
    }
  }
}
@media (max-width: 1024px){
  .second-screen{
    display: flex;
    .container{
      width: 100%;
    }
  }
  .second-screen-head{
    padding: 30px 0 0;
    .title-wrap{
      margin: 0;
    }
  }
}
@media (max-width: 991px){
  .prizes-carousel .item .title{
    font-size: 18px;
  }
}
@media (max-width: 767px){
  .second-screen{
    background-image: url(../images/bg-second-m.png);
    background-position: bottom center;
  }
  .second-screen-head{
    margin-bottom: 30px;
  }
  .prizes-carousel{
    margin-bottom: 10px;
  }
  .second-screen-head .title-wrap{
    text-align: center;
    padding-right: 55px;
  }
  .second-screen-head .title-wrap img{
    max-width: 100%;
    width: 490px;
    margin: 0;
  }
  .prizes-carousel .item .title{
    font-size: 25px;
  }
  .prizes-carousel .slick-arrow{
    width: 31px;
    background-size: auto;
  }
}

@media (max-width: 639px){
  .second-screen{
    min-height: 557px;
  }
  .second-screen-body h3, .second-screen-body .h3{
    margin-bottom: 20px;
  }
  .prizes-carousel .slick-center .item{
    transform: none;
