/* checkbox/radio */
input[type='checkbox'], input[type='radio'] {
  vertical-align: top;
  width: 20px;
  height: 20px;
  margin: 0 3px 0 0; }

input[type='checkbox'] + label, input[type='radio'] + label {
  cursor: pointer;
  padding-left: 34px;
  padding-top: 0px;
  position: relative;
  margin-bottom: 0px;
  display: block;
  line-height: 1.3em;
  color: #4fb748;
  font-size: 12px; }
  input[type='checkbox'] + label:before, input[type='radio'] + label:before {
    content: '';
    position: absolute;
    left: 0px;
    top: 50%;
    margin-top: -9px;
    width: 18px;
    height: 18px;
    border: 2px solid #4fb748;
    border-radius: 4px;
    border-radius: 50%; }
  input[type='checkbox'] + label:after, input[type='radio'] + label:after {
    content: '';
    position: absolute;
    left: 3px;
    top: 50%;
    margin-top: -6px;
    width: 12px;
    height: 12px;
    opacity: 0;
    background: #4fb748;
    border-radius: 2px;
    border-radius: 50%; }

input[type='radio'] + label:before {
  border-radius: 50%; }

input[type='radio'] + label:after {
  border-radius: 50%; }

input[type='checkbox']:not(checked), input[type='radio']:not(checked) {
  position: absolute;
  opacity: 0;
  left: 0;
  top: 0;
  z-index: -1; }

input[type='checkbox']:checked + label:after, input[type='radio']:checked + label:after {
  opacity: 1; }

