/**/
*{
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
}
img{
	border:0;
	max-width:100%;
	height: auto;
}
a{
	color: #fff;
	text-decoration: none;
	transition: color 0.4s, background 0.4s, border 0.4s;
}
a:hover{
	color: #fff;
	text-decoration: underline;
}
a:focus{
 	text-decoration: none;
}
*:focus,*:active, * {
	outline: none !important;
}
*::-moz-selection {
  background: rgba(211, 0, 4, 0.3); }

*::selection {
  background: rgba(211, 0, 4, 0.3); }

body{
	font: 14px $font-1m;
	line-height: 1.5;
	color: #000;
	background: $color-1; 
	padding: 0;
}

#wrapper{
	overflow: hidden;
	position: relative;
}
.container{
	max-width: 1560px;
	padding: 0 15px;
	margin: 0 auto;
	position: relative;
	z-index: 10;
}

.text-center{
	text-align: center;
}

/**/
h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5{
	margin-top: 0px;
    margin-bottom: 0.8em;
}
