h4, .h4{

}
h5, .h5{

}
h6, .h6{

}

strong{
	font-weight: normal;
	font-family: $font-1b;
}

/* table */
table{
	border-collapse: collapse;
	width: 100%;
	margin: 0px 0 15px;
}
table th{
	padding: 5px 5px;
	text-align: center;
	font-family: $font-1b;
	text-transform: uppercase;
	font-size: 14px;
	line-height: 1.3em;
}
table td{
	padding: 0px 5px;
	font-size: 12px;
	line-height: 1.2em;
	color: #4fb748;
}